a {
  color: #333;
  max-width: 150px;
  background-color: #0000;
  border-radius: 320px;
  margin-bottom: 20px;
  padding: 8px 12px;
  font-size: 16px;
  font-weight: bold;
  text-decoration: none;
}

a:hover {
  box-shadow: -2px -2px 5px #fff, 2px 2px 5px #babecc;
}

.form {
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  display: flex;
}

label {
  flex-direction: column;
  margin-top: 10px;
  display: flex;
}

label > input[type="number"] {
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 5px;
  padding: 5px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #07c;
  border: none;
  border-radius: 5px;
  margin-top: 20px;
  padding: 10px 20px;
}

button[type="submit"]:hover {
  background-color: #005fa3;
}

